"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";

type Message = {
  role: "user" | "bot";
  content: string;
};

export default function ChatPage() {
  const router = useRouter();

  const [messages, setMessages] = useState<Message[]>([]);
  const [input, setInput] = useState("");
  const [loading, setLoading] = useState(false);
  const [username, setUsername] = useState<string | null>(null);

  const [showLoginModal, setShowLoginModal] = useState(false);
  const [showRegisterModal, setShowRegisterModal] = useState(false);

  //  Proteger ruta + cargar historial
  useEffect(() => {
    const token = localStorage.getItem("token");
    const storedUsername = localStorage.getItem("username");

    if (token && storedUsername) {
      setUsername(storedUsername);
      fetchHistory(token);
    }
  }, []);

  //  Obtener historial
  const fetchHistory = async (token: string) => {
    try {
      const res = await fetch("http://localhost:3000/chat/history", {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      const data = await res.json();

      if (!res.ok) {
        throw new Error(data.error || "Error cargando historial");
      }

      setMessages(data.messages);
    } catch (error) {
      console.error("Error cargando historial:", error);
    }
  };

  //  Enviar mensaje
  const sendMessage = async () => {
    if (!input.trim()) return;

    const token = localStorage.getItem("token");
    if (!token) {
      setShowLoginModal(true);
      return;
    }

    const newMessages: Message[] = [
      ...messages,
      { role: "user", content: input },
    ];

    setMessages(newMessages);
    setInput("");
    setLoading(true);

    try {
      const res = await fetch("http://localhost:3000/chat", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify({
          messages: [{ role: "user", content: input }],
        }),
      });

      const data = await res.json();

      if (!res.ok) {
        throw new Error(data.error || "Error en el chat");
      }

      setMessages([
        ...newMessages,
        { role: "bot", content: data.reply },
      ]);
    } catch (error) {
      console.error("Error enviando mensaje:", error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="flex min-h-screen flex-col bg-zinc-50 dark:bg-black">
      {/* HEADER */}
      <header className="border-b bg-white p-4 dark:bg-zinc-900">
        <div className="mx-auto flex max-w-4xl items-center justify-between">
          <h1 className="text-lg font-bold">ChatBot </h1>

          {username ? (
            <div className="flex items-center gap-4 text-sm">
              <span className="text-gray-600 dark:text-gray-300">
                 {username}
              </span>

              <button
                onClick={() => {
                  localStorage.removeItem("token");
                  localStorage.removeItem("username");
                  setUsername(null);
                  setMessages([]);
                }}
                className="rounded border px-3 py-1 hover:bg-zinc-100 dark:hover:bg-zinc-800"
              >
                Cerrar sesi贸n
              </button>
            </div>
          ) : (
            <div className="flex gap-3 text-sm">
              <button
                onClick={() => setShowLoginModal(true)}
                className="rounded border px-3 py-1 hover:bg-zinc-100 dark:hover:bg-zinc-800"
              >
                Iniciar sesi贸n
              </button>

              <button
                onClick={() => setShowRegisterModal(true)}
                className="rounded bg-black px-3 py-1 text-white hover:opacity-90 dark:bg-white dark:text-black"
              >
                Registrarse
              </button>
            </div>
          )}
        </div>
      </header>

      {/* MENSAJES */}
      <main className="mx-auto flex w-full max-w-4xl flex-1 flex-col gap-3 overflow-y-auto p-4">
        {messages.map((m, i) => (
          <div
            key={i}
            className={`max-w-xl rounded-lg px-4 py-2 ${
              m.role === "user"
                ? "ml-auto bg-black text-white dark:bg-white dark:text-black"
                : "mr-auto bg-zinc-200 dark:bg-zinc-800"
            }`}
          >
            {m.content}
          </div>
        ))}

        {loading && (
          <div className="mr-auto max-w-xl rounded-lg bg-zinc-200 px-4 py-2 dark:bg-zinc-800">
            Escribiendo...
          </div>
        )}
      </main>

      {/* INPUT */}
      <footer className="border-t bg-white p-4 dark:bg-zinc-900">
        <div className="mx-auto flex max-w-4xl gap-2">
          <input
            type="text"
            value={input}
            onChange={(e) => setInput(e.target.value)}
            onKeyDown={(e) => e.key === "Enter" && sendMessage()}
            placeholder="Escribe un mensaje..."
            className="flex-1 rounded border px-3 py-2"
          />
          <button
            onClick={sendMessage}
            disabled={loading}
            className="rounded bg-black px-4 py-2 text-white disabled:opacity-50 dark:bg-white dark:text-black"
          >
            Enviar
          </button>
        </div>
      </footer>

      {/* MODAL LOGIN */}
      {showLoginModal && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-50">
          <div className="w-full max-w-md rounded bg-white p-6 dark:bg-zinc-900">
            <h2 className="mb-4 text-lg font-bold">Iniciar sesi贸n</h2>
            <form
              onSubmit={async (e) => {
                e.preventDefault();
                const form = e.target as HTMLFormElement;
                const email = (form.email as HTMLInputElement).value;
                const password = (form.password as HTMLInputElement).value;

                try {
                  const res = await fetch("http://localhost:3000/auth/login", {
                    method: "POST",
                    headers: { "Content-Type": "application/json" },
                    body: JSON.stringify({ email, password }),
                  });
                  const data = await res.json();

                  if (!res.ok) throw new Error(data.error || "Error login");

                  localStorage.setItem("token", data.token);
                  localStorage.setItem("username", data.username);
                  setUsername(data.username);
                  setShowLoginModal(false);
                  fetchHistory(data.token);
                } catch (err) {
                  console.error(err);
                  alert("Credenciales incorrectas");
                }
              }}
            >
              <input
                type="email"
                name="email"
                placeholder="Email"
                className="mb-2 w-full rounded border px-3 py-2"
                required
              />
              <input
                type="password"
                name="password"
                placeholder="Contrase帽a"
                className="mb-4 w-full rounded border px-3 py-2"
                required
              />
              <div className="flex justify-end gap-2">
                <button
                  type="button"
                  onClick={() => setShowLoginModal(false)}
                  className="rounded border px-3 py-1 hover:bg-zinc-100 dark:hover:bg-zinc-800"
                >
                  Cancelar
                </button>
                <button
                  type="submit"
                  className="rounded bg-black px-3 py-1 text-white hover:opacity-90 dark:bg-white dark:text-black"
                >
                  Iniciar sesi贸n
                </button>
              </div>
            </form>
          </div>
        </div>
      )}

      {/* MODAL REGISTER */}
      {showRegisterModal && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-50">
          <div className="w-full max-w-md rounded bg-white p-6 dark:bg-zinc-900">
            <h2 className="mb-4 text-lg font-bold">Registrarse</h2>
            <form
              onSubmit={async (e) => {
                e.preventDefault();
                const form = e.target as HTMLFormElement;
                const username = (form.username as HTMLInputElement).value;
                const email = (form.email as HTMLInputElement).value;
                const password = (form.password as HTMLInputElement).value;

                try {
                  const res = await fetch("http://localhost:3000/auth/register", {
                    method: "POST",
                    headers: { "Content-Type": "application/json" },
                    body: JSON.stringify({ username, email, password }),
                  });
                  const data = await res.json();

                  if (!res.ok) throw new Error(data.error || "Error registro");

                  localStorage.setItem("token", data.token);
                  localStorage.setItem("username", data.username);
                  setUsername(data.username);
                  setShowRegisterModal(false);
                  fetchHistory(data.token);
                } catch (err) {
                  console.error(err);
                  alert("Error al registrar usuario");
                }
              }}
            >
              <input
                type="text"
                name="username"
                placeholder="Nombre de usuario"
                className="mb-2 w-full rounded border px-3 py-2"
                required
              />
              <input
                type="email"
                name="email"
                placeholder="Email"
                className="mb-2 w-full rounded border px-3 py-2"
                required
              />
              <input
                type="password"
                name="password"
                placeholder="Contrase帽a"
                className="mb-4 w-full rounded border px-3 py-2"
                required
              />
              <div className="flex justify-end gap-2">
                <button
                  type="button"
                  onClick={() => setShowRegisterModal(false)}
                  className="rounded border px-3 py-1 hover:bg-zinc-100 dark:hover:bg-zinc-800"
                >
                  Cancelar
                </button>
                <button
                  type="submit"
                  className="rounded bg-black px-3 py-1 text-white hover:opacity-90 dark:bg-white dark:text-black"
                >
                  Registrarse
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}
